import React, { Component } from 'react';
import './App.css';
import Jumbotron from "./components/Jumbotron";
import Brands from "./components/Brands";
import Models from "./components/Models";
import Engines from "./components/Engines";
import Packages from "./components/Packages";
import Offers from "./components/Offers";
import Email from "./components/Email";

class App extends Component {
  constructor(props) {
    super(props);
    this.change = this.change.bind(this);
    this.state = {
      step: 'jumbotron'
    };
  }

  change(step) {
    this.setState({
      step: step
    });
  }

  render() {
    switch (this.state.step) {
      case 'brands':
        return (<Brands next={() => this.change('models')}/>);
      case 'models':
        return (<Models next={() => this.change('engines')}/>);
      case 'engines':
        return (<Engines next={() => this.change('packages')}/>);
      case 'packages':
        return (<Packages next={() => this.change('email')}/>);
      case 'email':
        return (<Email next={() => this.change('email-given')} email={null} />);
      case 'email-given':
        return (<Email next={() => this.change('offers')} email={"marcin.kowalski@example.com"} />);
      case 'offers':
        return (<Offers next={() => this.change('contacted')}/>);
      case 'contacted':
        alert('Twoje dane kontaktowe zostaly przekazane do dealera');
        return (<Offers next={() => this.change('contacted')}/>);
      default:
        return (<Jumbotron next={() => this.change('brands')}/>);
    }

  }
}



export default App;
